.login-btn {
  background: none;
  transition: all ease-in-out 0.5s;
  position: relative;
}

.login-btn::before {
  content: '';
  width: 0;
  z-index: -20;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  background-image: linear-gradient(90deg, #ff4eb5 0%, #ffa065 100%);
  transition: all ease-in-out 0.5s;
}

.login-btn:hover::before {
  width: 50%;
}

.login-btn:hover {
  color: white !important;
}

.login-btn::after {
  content: '';
  width: 0;
  z-index: -20;
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  background-image: linear-gradient(270deg, #ffa065 0%, #ffa065 100%);
  transition: all ease-in-out 0.5s;
  transform: rotate(180deg);
}

.login-btn:hover::after {
  width: 52%;
}

